  const leftHours = hoursDiff - daysDiff * 24;
  const simpleFormat = (value: number) => String(value).padStart(2);
  const formattedDate = `${
    daysDiff !== 0 ? "Há " + simpleFormat(daysDiff) + " dias e " : ""
  }${simpleFormat(leftHours) + " horas"}`;

  function calculateTime() {
    const oldDate = utcToZonedTime(
      new Date(post?.pilar?.data_inclusao),
      "America/Sao_Paulo"
    );
    const dateFormatted = formatDistanceToNow(oldDate, {
      locale: ptBR,
      addSuffix: true,
    });
    return dateFormatted;
  }

